<template>
  <!-- 聊天信息 -->
  <view class="chat-message">
    <!-- 对方的信息 -->
    <view class="to" v-if="!isMe">
      <view class="left">
        <image class="avatar" src="../../static/headIcon.jpeg"></image>
      </view>
      <view class="right">
        <!-- 内容 -->
        <view class="content">
          <text>明天约吗，去购物公园</text>
        </view>
      </view>
    </view>
    <!-- 自己发送的信息 -->
    <view class="me" v-else>
      <view class="left">
        <image class="avatar" src="../../static/headIcon.jpeg"></image>
      </view>
      <view class="right">
        <!-- 内容 -->
        <view class="content">
          <text>明天约吗，去购物公园</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  name: "ChatMessage",
  props: ["isMe"],
  data() {
    return {};
  },
};
</script>

<style lang='scss' scoped>
.chat-message {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 10px;

  .to, .me {
    display: flex;
    align-items: center;
    max-width: 80%;
    .left {
      box-sizing: border-box;
      width: 50px;
      height: 50px;

      image {
        width: 45px;
        height: 45px;
        border-radius: 50%;
      }
    }
    .right {
      margin-left: 10px;
      .content {
        position: relative;
        min-height: 30px;
        padding: 5px 10px;
        display: flex;
        align-items: center;
        background-color: rgb(179, 206, 213);
        border-radius: 5px;

        &::after {
          content: "";
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: -18px;
          width: 0px;
          height: 0px;
          border: 10px solid transparent;
          border-right-color: rgb(179, 206, 213);
        }
      }
    }
  }
  .me {
    align-self: flex-end;
    flex-direction: row-reverse;
    .right {
      margin-left: 0;
      margin-right: 10px;
      .content {

        &::after {
          content: "";
          left: calc(100% - 2px);
          border: 10px solid transparent;
          border-left-color: rgb(179, 206, 213);
        }
      }
    }
  }
}
</style>